import {
  RadarChart,
  PolarGrid,
  PolarAngleAxis,
  Radar,
  ResponsiveContainer,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
} from "recharts";
import { TopLeft1, TopLeft2, TopRight1, TopRight2, TopRight3 } from "./Icons";

const ResultDownloadTemplate = ({ resultRef, data, asset }) => {
  const { idx, name, score, vals, result, computed, resultDesc } = data;

  const svgWidth = 836;
  const svgHeight = 60;
  const barY = 20;
  const barHeight = 20;
  const barRadius = 10;
  const markerX = 20 + computed.valence * (svgWidth - 40);
  const markerY = svgHeight / 2;
  const scale = 1;

  return (
    <div
      ref={resultRef}
      style={{ width: "1080px" }}
      className={`relative flex flex-col items-center text-center py-10 overflow-hidden ${
        result[idx].id === 0
          ? `${vals.q6 > 0 ? "bg-end-0 result" : "bg-end-0"}`
          : result[idx].id === 1
          ? `${vals.q6 > 0 ? "bg-end-1 result" : "bg-end-1"}`
          : result[idx].id === 2
          ? `${vals.q6 > 0 ? "bg-end-2 result" : "bg-end-2"}`
          : `${vals.q6 > 0 ? "bg-end-3 result" : "bg-end-3"}`
      }`}
    >
      <TopRight1
        width={600}
        height={600}
        className={`absolute top-48 -right-20 right-top1--answer${result[idx].id}`}
      />
      <TopRight2
        width={950}
        height={950}
        className={`absolute top-52 -right-32 right-top2--answer${result[idx].id}`}
      />
      <TopRight3
        width={450}
        height={450}
        className={`absolute top-[1230px] -right-16 right-top2--answer${result[idx].id}`}
      />
      <div className="absolute -top-12 -right-5">
        <img
          src={asset("/background/topBG.png")}
          alt="decorative"
          className="w-full"
        />
      </div>
      <TopLeft1
        width={550}
        height={550}
        className={`absolute top-[650px] -left-20 left-top1--answer${result[idx].id}`}
      />
      <TopLeft2
        width={500}
        height={500}
        className={`absolute top-[1250px] -left-12 left-top2--answer${result[idx].id}`}
      />

      {vals.q6 > 0 && (
        <>
          <div className="absolute bottom-12 w-[900px]">
            <img
              src={asset("/background/bottomStar.png")}
              alt="bottomStar decorative"
              className="w-full h-full object-cover"
            />
          </div>
          <div className="absolute bottom-52 -left-32 w-[600px]">
            <img
              src={asset("/background/leftDown.png")}
              alt="leftDown decorative"
              className="w-full h-full object-cover"
            />
          </div>
          <div className="absolute bottom-16 -right-60 w-[950px]">
            <img
              src={asset("/background/rightDown.png")}
              alt="rightDown decorative"
              className="w-full h-full object-cover"
            />
          </div>
        </>
      )}

      <div className="relative w-full max-w-[900px] mt-20 mx-auto">
        <div className="absolute top-20 left-10 flex flex-col items-start">
          <img
            src={asset("/background/topStar.png")}
            alt="decorative"
            className="w-36 h-auto"
          />
        </div>
        <div className="absolute top-10 left-32 flex flex-col items-end">
          <img
            src={asset("/background/topStar.png")}
            alt="decorative"
            className="w-20 h-auto"
          />
        </div>

        <div className="relative flex flex-col items-center justify-center gap-5 w-full px-4">
          <div className="relative z-20 w-full flex flex-col items-start gap-5 text-4xl text-white ml-3 mt-52">
            <p>
              {name}{score}
            </p>
            <p className="ml-24">
               <span className="font-black">{result[idx].type}</span> 
            </p>
            <p className="font-black ml-64">{result[idx].role}</p>
          </div>
          <div className="absolute -top-8 -right-5 z-10">
            <div className="relative">
              <div className="absolute top-0 right-8 w-[570px] h-auto rounded-2xl overflow-hidden">
                <img
                  src={asset(`/text-${idx}.png`)}
                  alt={`text ${idx}`}
                  className="w-full h-full object-cover"
                />
              </div>
              <div className="w-[500px] h-[500px]">
                <img
                  src={asset(`/result_${idx}.png`)}
                  alt={`Result ${idx}`}
                  className="w-full h-full object-cover"
                />
              </div>
            </div>
          </div>

          {/*  */}
          <div className="relative w-full mt-12">
            <div className="w-full h-auto aspect-[849/206] overflow-hidden">
              <img
                src={asset("/descBG.png")}
                alt="desc bg"
                className="w-full h-full"
              />
            </div>
            <p className="absolute top-1/2 transform -translate-y-1/2 text-justify text-2xl text-gray font-light leading-relaxed tracking-widest px-28">
              {resultDesc[idx]}
            </p>
          </div>
        </div>

        <div className="mx-auto relative w-full flex flex-col justify-center gap-4 mt-5 px-0">
          <p className="absolute top-0 left-0 text-start text-2xl font-black text-white tracking-widest">
            
          </p>
          <div className="w-[836px] mx-auto">
            <svg
              viewBox={`-20 -20 ${svgWidth + 40} ${svgHeight + 40}`}
              width="100%"
              height="auto"
              preserveAspectRatio="xMidYMid meet"
            >
              <rect
                x="4"
                y={barY - 4}
                width={svgWidth - 8}
                height={barHeight + 8}
                rx={barRadius + 2}
                fill="none"
                stroke="white"
                strokeWidth="2"
              />
              <defs>
                <linearGradient id="energyGrad" x1="0" y1="0" x2="1" y2="0">
                  <stop offset="0%" stopColor="#F2E15E" />
                  <stop offset="100%" stopColor="#00897B" />
                </linearGradient>
              </defs>

              <rect
                x="8"
                y={barY}
                width={svgWidth - 16}
                height={barHeight}
                rx={barRadius}
                fill="url(#energyGrad)"
                clipPath="url(#barClip)"
              />

              <g transform={`translate(${markerX}, ${markerY})`}>
                <path
                  d="M19.5628 0L0 34.6059L19.5628 25.9523L39.1342 34.6059L19.5628 0Z"
                  fill="white"
                  transform={`translate(20, 17.5) scale(${scale}) translate(-20, -17.5)`}
                />
              </g>
            </svg>
          </div>
          <p
            className={`text-xl ${
              result[idx].id === 1 || result[idx].id === 3
                ? "text-white"
                : "text-gray"
            } text-justify leading-relaxed font-light px-0`}
          >
            
            <br />
            
          </p>
        </div>

        <div className="w-full flex flex-row items-center justify-between gap-8 mt-10 px-5">
          <div className="relative overflow-hidden w-[400px] h-auto aspect-[394/410]">
            <svg
              width="100%"
              height="100%"
              viewBox="0 0 394 410"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
              className="absolute inset-0 w-full h-full"
            >
              <path
                d="M324.754 408H68.5282C31.5615 408 1.30566 377.744 1.30566 340.778V69.102C1.30566 32.1353 31.5615 1.87939 68.5282 1.87939H324.754C361.721 1.87939 391.976 32.1353 391.976 69.102V340.778C391.976 377.744 361.721 408 324.754 408Z"
                stroke="white"
                stroke-width="2.61126"
                stroke-miterlimit="10"
              />
              <path
                id="header-bg"
                opacity="0.75"
                d="M391.976 104.746H1.30566V67.2225C1.30566 30.2558 31.5615 0 68.5282 0H324.754C361.721 0 391.976 30.2558 391.976 67.2225V104.746Z"
                fill="white"
              />
            </svg>
            <div
              className={`absolute top-10 left-0 w-full flex items-center justify-center ${
                result[idx].id === 0
                  ? "text-result0"
                  : result[idx].id === 1
                  ? "text-result1"
                  : result[idx].id === 2
                  ? "text-result2"
                  : "text-result3"
              } text-2xl sm:text-3xl font-black`}
            >
              
            </div>
            <div className="absolute w-full h-full pt-[35%] bottom-2 pointer-events-none select-none no-focus">
              <ResponsiveContainer width="100%" height="100%">
                <RadarChart
                  cx="50%"
                  cy="50%"
                  outerRadius="80%"
                  data={computed.radarData}
                  fill="#FFFFFF"
                >
                  <PolarGrid stroke="white" />
                  <PolarAngleAxis
                    dataKey="subject"
                    tick={{ fill: "#FFFFFF", fontSize: 22 }}
                  />
                  <Radar
                    name=""
                    dataKey="A"
                    stroke="#FFFFFF"
                    fill="#FFFFFF"
                    fillOpacity={0.5}
                    isAnimationActive={false}
                  />
                </RadarChart>
              </ResponsiveContainer>
            </div>
          </div>

          <div className="relative w-[400px] h-auto aspect-[394/410]">
            <svg
              width="100%"
              height="100%"
              viewBox="0 0 394 410"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M324.754 408H68.5282C31.5615 408 1.30566 377.744 1.30566 340.778V69.102C1.30566 32.1353 31.5615 1.87939 68.5282 1.87939H324.754C361.721 1.87939 391.976 32.1353 391.976 69.102V340.778C391.976 377.744 361.721 408 324.754 408Z"
                stroke="white"
                stroke-width="2.61126"
                stroke-miterlimit="10"
              />
              <path
                opacity="0.75"
                d="M391.976 104.746H1.30566V67.2225C1.30566 30.2558 31.5615 0 68.5282 0H324.754C361.721 0 391.976 30.2558 391.976 67.2225V104.746Z"
                fill="white"
              />
            </svg>
            <p
              className={`absolute top-10 left-0 w-full flex items-center justify-center ${
                result[idx].id === 0
                  ? "text-result0"
                  : result[idx].id === 1
                  ? "text-result1"
                  : result[idx].id === 2
                  ? "text-result2"
                  : "text-result3"
              } text-2xl sm:text-3xl font-black`}
            >
              
            </p>
            <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 w-full h-full pt-[30%] pl-[30px] pr-[50px] pb-[20px]">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart
                  layout="vertical"
                  data={computed.composition}
                  margin={{ top: 0, right: 20, left: 20, bottom: 0 }}
                >
                  <CartesianGrid
                    strokeDasharray="3 3"
                    horizontal={false}
                    stroke="white"
                  />
                  <XAxis
                    type="number"
                    domain={[0, 4]}
                    tick={{ fill: "#FFFFFF", fontSize: 18 }}
                  />
                  <YAxis
                    type="category"
                    dataKey="name"
                    tick={{ fill: "#FFFFFF", fontSize: 16 }}
                  />
                  <Bar
                    dataKey="value"
                    fill={
                      result[idx].id === 0
                        ? "#65AFB4"
                        : result[idx].id === 1
                        ? "#0B3F84"
                        : result[idx].id === 2
                        ? "#E8A04A"
                        : "#E43121"
                    }
                    radius={[0, 8, 8, 0]}
                    isAnimationActive={false}
                  />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </div>
        </div>

        {vals.q6 > 0 && (
          <>
            <p
              className={`${
                result[idx].id === 2 ? "text-white" : "text-[#3E3A39]"
              } px-5 text-left font-light leading-loose tracking-normal mt-5`}
            >
              
              <br />
              
            </p>

            <div className="w-full grid grid-cols-2 items-center justify-center gap-3 px-8 mt-5">
              {Array.from({ length: 6 }).map((_, i) => (
                <div key={i} className="w-96 h-auto aspect-[440/490] mx-auto">
                  <img
                    src={asset(`/SOS-${i + 1}.png`)}
                    alt={`SOS-${i + 1}`}
                    className="w-full h-full object-cover"
                  />
                </div>
              ))}
            </div>
          </>
        )}

        <div className="h-16 w-full"></div>
      </div>
    </div>
  );
};

export default ResultDownloadTemplate;
